import React from "react"
import Parser from "../components/parser"
import Layout from "../components/layout"
import Head from "../components/head"
import SEO from "../components/seo"
import { 
  Container, 
  Typography, 
  Box, 
  Card, 
  CardMedia, 
  CardContent,
  Chip,
  Divider,
  Paper
} from '@mui/material'
import { ArrowBack as ArrowBackIcon } from '@mui/icons-material'
import { Link } from 'gatsby'

const Post = ({ pageContext }) => {
  const post = pageContext.article;
  const featuredImg = post.featuredImage?.url;

  const content = post.content || post.excerpt

  return (
    <Layout>
      <Head pageTitle={post.title} />
      <SEO title={post.title} />

      <Container maxWidth="lg" sx={{ py: 4, mt: 8 }}>
        {/* Back to Home Link */}
        <Box sx={{ mb: 3 }}>
          <Link 
            to="/" 
            style={{ 
              textDecoration: 'none', 
              color: 'inherit',
              display: 'flex',
              alignItems: 'center',
              gap: 1,
              width: 'fit-content'
            }}
          >
            <ArrowBackIcon fontSize="small" />
            <Typography variant="body2" color="text.secondary">
              Back to Home
            </Typography>
          </Link>
        </Box>

        {/* Article Header */}
        <Box sx={{ mb: 4 }}>
          <Typography 
            variant="h2" 
            component="h1" 
            gutterBottom
            sx={{ 
              fontWeight: 600,
              mb: 2
            }}
          >
            <Parser data={post.title} />
          </Typography>
          
          {/* Category and Date */}
          <Box sx={{ display: 'flex', gap: 2, alignItems: 'center', mb: 3 }}>
            {post.categories && post.categories[0] && (
              <Chip 
                label={post.categories[0].name} 
                color="primary" 
                variant="outlined"
                size="small"
              />
            )}
            {post.date && (
              <Typography variant="body2" color="text.secondary">
                {new Date(post.date).toLocaleDateString('en-US', {
                  year: 'numeric',
                  month: 'long',
                  day: 'numeric'
                })}
              </Typography>
            )}
          </Box>
        </Box>

        {/* Featured Image */}
        {featuredImg && (
          <Box sx={{ mb: 4 }}>
            <Card sx={{ overflow: 'hidden' }}>
              <CardMedia
                component="img"
                height="400"
                image={featuredImg}
                alt={post.slug}
                sx={{ 
                  objectFit: 'cover',
                  width: '100%'
                }}
              />
            </Card>
          </Box>
        )}

        {/* Article Content */}
        <Paper 
          elevation={0} 
          sx={{ 
            p: 4, 
            backgroundColor: 'background.paper',
            borderRadius: 2,
            border: '1px solid',
            borderColor: 'divider'
          }}
        >
          <Box 
            sx={{
              '& h1, & h2, & h3, & h4, & h5, & h6': {
                fontWeight: 600,
                mb: 2,
                mt: 3
              },
              '& h1': { fontSize: '2rem' },
              '& h2': { fontSize: '1.75rem' },
              '& h3': { fontSize: '1.5rem' },
              '& p': {
                mb: 2,
                lineHeight: 1.7
              },
              '& ul, & ol': {
                mb: 2,
                pl: 3
              },
              '& li': {
                mb: 1
              },
              '& strong': {
                fontWeight: 600
              },
              '& a': {
                color: 'primary.main',
                textDecoration: 'none',
                '&:hover': {
                  textDecoration: 'underline'
                }
              }
            }}
          >
            <Parser data={content} />
          </Box>
        </Paper>
      </Container>
    </Layout>
  )
}

export default Post
